import React from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Register from "./Register"; //  Register
import Home from "./home"; //  Home
import ProfileSetup from "./ProfileSetup"; //  ProfileSetup
import Report from "./AdminReport"; //  Report
import AdminStatSlip from "./Stat-slip"; //  component  export
import ImageQueue from "./ImageQueue";
import TimeHistory from "./TimeHistory";
import CheckHistory from "./CheckHistory";  //  CheckHistory
import LuckyWheel from "./LuckyWheel.js";
import Giftsetting from "./Giftsetting";
function App() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<Register />} /> {/*  */}
        <Route path="/home" element={<Home />} /> {/*  Home */}
        <Route path="/report" element={<Report />} /> {/*  Report */}
        <Route path="/profile-setup" element={<ProfileSetup />} /> {/*  */}
        <Route path="/stat-slip" element={<AdminStatSlip />} />
        <Route path="/image-queue" element={<ImageQueue />} />
        <Route path="/TimeHistory" element={<TimeHistory />} />
        <Route path="/check-history" element={<CheckHistory />} /> {/*  */}
        <Route path="/lucky-wheel" element={<LuckyWheel />} />
        <Route path="/gift-setting" element={<Giftsetting />} />
      </Routes>
    </Router>
  );
}

export default App;
