import React, { useEffect, useState } from "react";
import "./CheckHistory.css";

function CheckHistory() {
  const [history, setHistory] = useState([]);
  const [selected, setSelected] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [editMode, setEditMode] = useState(false);

  useEffect(() => {
    fetchHistory();
  }, []);

  const fetchHistory = () => {
    fetch("http://localhost:5001/api/check-history")
      .then((res) => res.json())
      .then((data) => setHistory(data));
  };

  const handleDelete = async (id) => {
    if (!window.confirm("?")) return;
    await fetch("http://localhost:5001/api/delete-history", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ id }),
    });
    fetchHistory();
  };

  const handleDeleteAll = async () => {
    if (!window.confirm("?")) return;
    await fetch("http://localhost:5001/api/delete-all-history", {
      method: "POST",
    });
    fetchHistory();
  };

  const textHistory = history.filter((item) => item.type === "text");
  const imageHistory = history.filter((item) => item.type === "image");

  const formatDate = (dateString) => {
    if (!dateString) return "-";
    return new Date(dateString).toLocaleString("th-TH", {
      day: "2-digit",
      month: "2-digit",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
    });
  };

  // Modal 
  const DetailModal = ({ item, onClose }) => (
    <div
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        width: "100vw",
        height: "100vh",
        background: "rgba(0,0,0,0.3)",
        zIndex: 9999,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <div
        style={{
          background: "#fff",
          borderRadius: 12,
          padding: 32,
          minWidth: 320,
          maxWidth: 400,
          boxShadow: "0 2px 24px 0 rgba(30,41,59,.18)",
        }}
      >
        <h2
          style={{
            fontSize: 20,
            marginBottom: 16,
            color: "#1e293b",
          }}
        >
          
        </h2>
        <div style={{ marginBottom: 8 }}>
          <b>ID:</b> {item.id}
        </div>
        {item.type && (
          <div style={{ marginBottom: 8 }}>
            <b>:</b> {item.type}
          </div>
        )}
        {item.text && (
          <div style={{ marginBottom: 8 }}>
            <b>:</b> {item.text}
          </div>
        )}
        {item.filePath && (
          <div style={{ marginBottom: 8 }}>
            <b>:</b>
            <br />
            <img
              src={`http://localhost:5001${item.filePath}`}
              alt="img"
              style={{
                maxWidth: 180,
                borderRadius: 8,
                marginTop: 4,
              }}
            />
          </div>
        )}
        {item.status && (
          <div style={{ marginBottom: 8 }}>
            <b>:</b> {item.status === "approved" ? "" : ""}
          </div>
        )}
        {item.sender && (
          <div style={{ marginBottom: 8 }}>
            <b>:</b> {item.sender}
          </div>
        )}
        {item.price !== undefined && (
          <div style={{ marginBottom: 8 }}>
            <b>:</b> {item.price}
          </div>
        )}
        {item.createdAt && (
          <div style={{ marginBottom: 8 }}>
            <b>:</b> {formatDate(item.createdAt)}
          </div>
        )}
        {item.checkedAt && (
          <div style={{ marginBottom: 8 }}>
            <b>:</b> {formatDate(item.checkedAt)}
          </div>
        )}
        <button
          onClick={onClose}
          style={{
            marginTop: 16,
            padding: "0.5rem 1.5rem",
            background: "#64748b",
            color: "#fff",
            border: "none",
            borderRadius: 8,
            cursor: "pointer",
          }}
        >
          
        </button>
      </div>
    </div>
  );

  return (
    <div className="ch-main-bg">
      <header className="ch-header">
        <div className="ch-header-title">CMS ADMIN</div>
        <div className="ch-header-center"></div>
        <div style={{ flex: 1 }}></div>
      </header>
      <main style={{ marginTop: "110px", width: "100%" }}>
        <div
          style={{
            width: "100%",
            maxWidth: 1020,
            display: "flex",
            justifyContent: "flex-end",
            gap: 12,
            marginTop: 8,
          }}
        >
          <button
            className={`ch-btn ch-btn-edit${
              editMode ? " active" : ""
            }`}
            onClick={() => setEditMode((v) => !v)}
          >
            {editMode ? "" : ""}
          </button>
          <button
            className="ch-btn ch-btn-deleteall"
            onClick={handleDeleteAll}
          >
            
          </button>
        </div>
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            gap: "2rem",
            width: "100%",
            justifyContent: "center",
            alignItems: "flex-start",
            flexWrap: "wrap",
          }}
        >
          {/*  () */}
          <div className="ch-card">
            <div className="ch-card-header">
              {" "}
              <span style={{ color: "#ec4899" }}></span>
            </div>
            {textHistory.length === 0 ? (
              <div
                style={{
                  textAlign: "center",
                  color: "#94a3b8",
                  padding: "3rem 0",
                  fontSize: "1.125rem",
                }}
              >
                
              </div>
            ) : (
              textHistory.map((item, idx) => (
                <div className="ch-card-section" key={item.id}>
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      gap: "0.5rem",
                    }}
                  >
                    <div style={{ color: "#1e293b" }}>
                      <b>:</b> {formatDate(item.checkedAt)}
                    </div>
                    <div style={{ color: "#1e293b" }}>
                      <b>:</b> {item.text}
                    </div>
                    <div style={{ color: "#1e293b" }}>
                      <b>:</b> {item.status === "approved" ? "" : ""}
                    </div>
                    <button
                      className="ch-btn-detail"
                      onClick={() => {
                        setSelected(item);
                        setShowModal(true);
                      }}
                    >
                      
                    </button>
                  </div>
                  {editMode && (
                    <button
                      className="ch-btn-delete"
                      onClick={() => handleDelete(item.id)}
                    >
                      
                    </button>
                  )}
                </div>
              ))
            )}
          </div>
          {/*  () */}
          <div className="ch-card">
            <div className="ch-card-header">
              {" "}
              <span style={{ color: "#6366f1" }}></span>
            </div>
            {imageHistory.length === 0 ? (
              <div
                style={{
                  textAlign: "center",
                  color: "#94a3b8",
                  padding: "3rem 0",
                  fontSize: "1.125rem",
                }}
              >
                
              </div>
            ) : (
              imageHistory.map((item, idx) => (
                <div className="ch-card-section" key={item.id}>
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      gap: "0.5rem",
                    }}
                  >
                    <div style={{ color: "#1e293b" }}>
                      <b>:</b> {formatDate(item.checkedAt)}
                    </div>
                    <div style={{ color: "#1e293b" }}>
                      <b>:</b> {item.text}
                    </div>
                    <div style={{ color: "#1e293b" }}>
                      <b>:</b> {item.status === "approved" ? "" : ""}
                    </div>
                    {item.filePath && (
                      <div>
                        <img
                          src={`http://localhost:5001${item.filePath}`}
                          alt="img"
                          style={{
                            maxWidth: 180,
                            marginTop: 8,
                            borderRadius: 8,
                            boxShadow: "0 1px 4px 0 rgba(30,41,59,.08)",
                          }}
                        />
                      </div>
                    )}
                    <button
                      className="ch-btn-detail"
                      onClick={() => {
                        setSelected(item);
                        setShowModal(true);
                      }}
                    >
                      
                    </button>
                  </div>
                  {editMode && (
                    <button
                      className="ch-btn-delete"
                      onClick={() => handleDelete(item.id)}
                    >
                      
                    </button>
                  )}
                </div>
              ))
            )}
          </div>
        </div>
      </main>
      {showModal && selected && (
        <div className="ch-modal-bg">
          <div className="ch-modal-content">
            <h2
              style={{
                fontSize: 20,
                marginBottom: 16,
                color: "#1e293b",
              }}
            >
              
            </h2>
            <div style={{ marginBottom: 8 }}>
              <b>ID:</b> {selected.id}
            </div>
            {selected.type && (
              <div style={{ marginBottom: 8 }}>
                <b>:</b> {selected.type}
              </div>
            )}
            {selected.text && (
              <div style={{ marginBottom: 8 }}>
                <b>:</b> {selected.text}
              </div>
            )}
            {selected.filePath && (
              <div style={{ marginBottom: 8 }}>
                <b>:</b>
                <br />
                <img
                  src={`http://localhost:5001${selected.filePath}`}
                  alt="img"
                  style={{
                    maxWidth: 180,
                    borderRadius: 8,
                    marginTop: 4,
                  }}
                />
              </div>
            )}
            {selected.status && (
              <div style={{ marginBottom: 8 }}>
                <b>:</b> {selected.status === "approved" ? "" : ""}
              </div>
            )}
            {selected.sender && (
              <div style={{ marginBottom: 8 }}>
                <b>:</b> {selected.sender}
              </div>
            )}
            {selected.price !== undefined && (
              <div style={{ marginBottom: 8 }}>
                <b>:</b> {selected.price}
              </div>
            )}
            {selected.createdAt && (
              <div style={{ marginBottom: 8 }}>
                <b>:</b> {formatDate(selected.createdAt)}
              </div>
            )}
            {selected.checkedAt && (
              <div style={{ marginBottom: 8 }}>
                <b>:</b> {formatDate(selected.checkedAt)}
              </div>
            )}
            <button
              className="ch-btn"
              style={{ background: "#64748b", marginTop: 16 }}
              onClick={() => setShowModal(false)}
            >
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default CheckHistory;
