import React, { useState, useEffect } from "react";
import { io } from "socket.io-client";
import "./home.css";

const socket = io("http://localhost:4005");

function Home() {
  const [systemOn, setSystemOn] = useState(true);
  const [enableImage, setEnableImage] = useState(true);
  const [enableText, setEnableText] = useState(true);
  const [enableGift, setEnableGift] = useState(true);
  const [mode, setMode] = useState("image");
  const [minute, setMinute] = useState("");
  const [second, setSecond] = useState("");
  const [price, setPrice] = useState("");

  useEffect(() => {
    socket.on("configUpdate", (config) => {
      setSystemOn(config.systemOn);
      setEnableImage(config.enableImage);
      setEnableText(config.enableText);
      setEnableGift(config.enableGift ?? true);
    });
    socket.emit("getConfig");
    return () => socket.off("configUpdate");
  }, []);

  // /
  const handleToggleSystem = () => {
    const newStatus = !systemOn;
    setSystemOn(newStatus);
    //  
    if (!newStatus) {
      setEnableImage(false);
      setEnableText(false);
      setEnableGift(false);
      socket.emit("adminUpdateConfig", {
        systemOn: newStatus,
        enableImage: false,
        enableText: false,
        enableGift: false,
      });
    } else {
      //  
      setEnableImage(true);
      setEnableText(true);
      setEnableGift(true);
      socket.emit("adminUpdateConfig", {
        systemOn: newStatus,
        enableImage: true,
        enableText: true,
        enableGift: true,
      });
    }
  };

  // /
  const handleToggleImage = () => {
    const newStatus = !enableImage;
    setEnableImage(newStatus);
    socket.emit("adminUpdateConfig", {
      enableImage: newStatus,
      systemOn,
      enableText,
      enableGift,
    });
  };

  // /
  const handleToggleText = () => {
    const newStatus = !enableText;
    setEnableText(newStatus);
    socket.emit("adminUpdateConfig", {
      enableText: newStatus,
      systemOn,
      enableImage,
      enableGift,
    });
  };

  const handleToggleGift = () => {
    const newStatus = !enableGift;
    setEnableGift(newStatus);
    socket.emit("adminUpdateConfig", {
      enableGift: newStatus,
      systemOn,
      enableImage,
      enableText,
    });
  };

  const handleSave = () => {
    if (!minute && !second) {
      alert(" 1 ");
      return;
    }
    if (!price) {
      alert("");
      return;
    }
    const packageData = {
      id: Date.now(),
      mode,
      date: new Date().toLocaleString(),
      duration: `${minute ? minute + " " : ""}${second ? (minute ? " " : "") + second + " " : ""}`,
      price: price,
    };
    socket.emit("addSetting", packageData);
    setMinute("");
    setSecond("");
    setPrice("");
    alert("");
  };

  return (
    <div className="admin-home-minimal">
      <header className="admin-header-minimal">
        <div className="brand-minimal">
          <svg width="36" height="36" viewBox="0 0 24 24" fill="none" stroke="#3949ab" strokeWidth="2">
            <rect x="2" y="3" width="20" height="14" rx="2" ry="2"/>
            <line x1="8" y1="21" x2="16" y2="21"/>
            <line x1="12" y1="17" x2="12" y2="21"/>
          </svg>
          <span className="brand-title">CMS ADMIN</span>
        </div>
        <nav className="nav-minimal">
          <a href="/TimeHistory"></a>
          <a href="/image-queue"></a>
          <a href="/report"></a>
          <a href="/check-history"></a>
          <a href="/lucky-wheel"></a>
          <a href="/gift-setting"></a>
        </nav>
      </header>

      <main className="admin-main-minimal">
        <div className="system-status-row">
          <span className="system-label">:</span>
          <div
            className={`switch-minimal ${systemOn ? "on" : "off"}`}
            onClick={handleToggleSystem}
            title={systemOn ? "" : ""}
          >
            <div className="switch-dot"></div>
          </div>
          <span className={`system-status-text ${systemOn ? "on" : "off"}`}>
            {systemOn ? "" : ""}
          </span>
        </div>
        {!systemOn && (
          <div className="system-off-msg-minimal">
             
          </div>
        )}

        {/* / */}
        <div className="function-toggle-row">
          <span>:</span>
          <button
            className={`toggle-btn-minimal${enableImage ? " on" : " off"}`}
            onClick={handleToggleImage}
            disabled={!systemOn}
          >
            {enableImage ? "" : ""}
          </button>
          <span>:</span>
          <button
            className={`toggle-btn-minimal${enableText ? " on" : " off"}`}
            onClick={handleToggleText}
            disabled={!systemOn}
          >
            {enableText ? "" : ""}
          </button>
          <span>:</span>
          <button
            className={`toggle-btn-minimal${enableGift ? " on" : " off"}`}
            onClick={handleToggleGift}
            disabled={!systemOn}
          >
            {enableGift ? "" : ""}
          </button>
        </div>

        <section className="setting-card-minimal">
          <h2></h2>
          <div className="mode-select-row">
            <button
              className={`mode-btn-minimal${mode === "image" ? " active" : ""}`}
              onClick={() => setMode("image")}
              disabled={!systemOn}
            >
              
            </button>
            <button
              className={`mode-btn-minimal${mode === "text" ? " active" : ""}`}
              onClick={() => setMode("text")}
              disabled={!systemOn}
            >
              
            </button>
          </div>
          <div className="input-row-minimal">
            <input
              type="number"
              min="1"
              max="59"
              placeholder=""
              value={minute}
              onChange={(e) => setMinute(e.target.value)}
              disabled={!systemOn}
              className="input-minimal"
            />
            <input
              type="number"
              min="1"
              max="59"
              placeholder=""
              value={second}
              onChange={(e) => setSecond(e.target.value)}
              disabled={!systemOn}
              className="input-minimal"
            />
            <input
              type="number"
              min="1"
              placeholder=" ()"
              value={price}
              onChange={(e) => setPrice(e.target.value)}
              disabled={!systemOn}
              className="input-minimal"
            />
          </div>
          <button
            className="save-btn-minimal"
            onClick={handleSave}
            disabled={!systemOn}
          >
            
          </button>
        </section>
      </main>
    </div>
  );
}

export default Home;